import HeadingButton from './components/headingButton';
import HeadingGroup from './components/headingGroup';
import headingSimpleButton from './components/headingSimpleButton';
import { HeadingLocale } from './types';

export default function toolbar(headingConfig) {
  const headingLocaleConfig = headingConfig.locale || {} as HeadingLocale;
  return {
    buttons: {
      default: HeadingButton,
      group: HeadingGroup,
      simple: headingSimpleButton,
    },
    locale: headingLocaleConfig,
  };
}
